<template>
                    <div class="border p-3">
                        <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
</template>

<script>
import DataService from '../../service/DataService';
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common'
export default {
    props:{
        popupCondition: {
            type: Boolean,
            required: true
        }
    },
    components: {
'tree-view':TreeSelectField
    },
    data() {
        return {

            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [                     
                        {
                            astype: "TreeSelectField",
                            label: this.$t('ParentLocation'),
                            name: "ParentLocation",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LocationName'),
                            name: "LocationName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange:()=>{}

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AddressLine1'),
                            name: "AddressLine1",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                            config: {                                
                                option_request_url:process.env.VUE_APP_COMMON_API+'/Common/GetDDLData?moduleName=CRM&type=COUNTRIES',
                                token:this.GetUserInfo.Token,
                                onChange: this.onChangeDropdown
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('PostalCode'),
                            name: "PostalCode",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email'),
                            name: "Email",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Phone1'),
                            name: "Phone1",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Phone2'),
                            name: "Phone2",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LocationCode'),
                            name: "LocationCode",
                            value: "",
                            placeholder: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Tag'),
                            name: "Tag",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        
                    ]
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-secondary",
                    text: "Skip",
                    onClick: this.Skip,
                    isVisible:false,
                    
                },
                
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save & New",
                    isVisible:false,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save & Next",
                    isVisible:false,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.ClosePopup
                }
                
            ],
            myObj: [],
            LocationData: [],
            StatusData:[
            {
                name:'Active',
                value :1001
            },
            {
                name:'Inactive',
                value :1002
            },
            ],
            StateData:[],
            TagData:[],
            CreatedAt: "",
            CreatedBy:"",
            ModifiedAt:"",
            ModifiedBy:"",
            DataLoaded:0,
            UserId:''
        }
    },
   
    created: async function () {
        debugger
        var vm = this;
       await vm.GetLocationByCompanyId();
        vm.GetStatusDDL();
       await vm.GetTagsByModuleId();
       await vm.AdvancedSetupFields()
       vm.UserId=vm.GetUserInfo.ID;
        vm.DataLoaded=1;
    },
    methods: {
        Skip:function(){
           var vm=this;
           vm.$parent.SkipTab(2);
        },
        async AdvancedSetupFields()
        {
            var vm=this;
            if(vm.popupCondition=='setup')
            {
                
                vm.FormSchema[0].Data[6].visibility=false;
                vm.FormSchema[0].Data[7].visibility=false;
                vm.FormSchema[0].Data[8].visibility=false;
                vm.FormSchema[0].Data[9].visibility=false;
                vm.FormSchema[0].Data[10].visibility=false;
                vm.FormSchema[0].Data[11].visibility=false;
                vm.FormSchema[0].Data[12].visibility=false;
                vm.buttons[0].isVisible=true;
                vm.buttons[1].isVisible=true;
                vm.buttons[2].isVisible=true;
                vm.buttons[3].isVisible=false;
                vm.buttons[4].isVisible=false;
            }

        },
       async GetTagsByModuleId()
        {
            var vm=this;
            var params = `modulename=Asset`
            await  DataService.GetTagsByModuleId(params)
                .then(function (response) {
                    vm.TagData = JSON.parse(response.data);
                    vm.TagData.data.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[12].config.options.push({
                            name: itm.TAG_NAME,
                            value: itm.TAG_ID  
                        })
                    })
                    console.log(' GetTagsByModuleId  call end');
                });
        },
        onChangeDropdown(evt)
        {            
            var vm=this;
            var params = `countryId=${evt.target.value}`
            DataService.GetStateListwithIDByCountryId(params)
                .then(function (response) {
                    vm.StateData = JSON.parse(response.data).data;
                    vm.StateData=vm.StateData.map(item=>{return {name:item.name,value:item.state_id}});
                    $.each(vm.FormSchema, function (index, item) {
                $.each(item.Data, function (index, element) {
                    if (element.name == 'State') {
                        element.value = '';
                        element.config.options = vm.StateData;
                    }
                });
            });
                });            
        },
       async GetLocationByCompanyId() {
            var vm = this;
            var params = `reqForm=`
          await  DataService.GetLocationByCompanyId(params)
                .then(function (response) {
                    vm.LocationData = JSON.parse(response.data).data;

                    var json =vm.MakeNormalArray(response.data).data;
            $.each(json, $.proxy(function (index, item) {
                var  groupId = item.PARENT_LOCATION_ID;
                                    var chkGroup = $.grep(vm.LocationData, function (element, index) {
                                        return element.id == groupId;
                                    });
                                    if (chkGroup.length == 0) {
                                    var returnedData = $.grep(json, function (element, index) {
                                            return element.PARENT_LOCATION_ID == groupId;
                                        });
                                        if (returnedData.length > 0 && returnedData[0].LOCATION_NAME) {                                         
                                            var objGroup = {
                                                id: groupId,
                                                label: returnedData[0].LOCATION_NAME,
                                                children: returnedData.map((item) => {                                              
                                            return {id:item.COMPANY_LOCATION_ID,label:item.LOCATION_NAME}
                                            })
                                            };
                                            (vm.FormSchema)[0].Data[0].config.options.push(objGroup);
                                        }
                                      }
                }))    
                });
        },
        GetStatusDDL()
        {
            var vm=this;
        vm.StatusData.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: item.name,
                            value: item.value  
                        })
                    })
        },
        ClosePopup() {
            var vm = this;
            vm.$parent.CloseLocationPopup();
        },
        onSubmit(e) {
            debugger
            var vm = this;
            var propValues = e;
            
            var url = `LocationName=${e.LocationName}&LocationId=0`
            DataService.LocationExist(url).then(function (response) {
                vm.LocationId=response.data.result
                if (response.data.result  > 0) {
                    vm.ShowAlert(vm.$t('Location already exist', [vm.$t('Location')]), "warning", true, vm.$t("Alert"));
                    
                }
                else
                {
                    let myObj ={
                LocationId:vm.LocationId,
                LocationName:e.LocationName,
                Country:parseInt(e.Country),
                State:e.State,
                Address1:e.AddressLine1,
                Address2:"",
                City:e.City,
                PinCode:e.PostalCode,
                Email:e.Email,
                Phone1:e.Phone1,
                Phone2:e.Phone2,
                CreatedAt:null,
                CreatedBy: vm.UserId ,
                ModifiedAt: null,
                ModifiedBy: vm.UserId,
                StatusId:e.Status,
                State_id:e.State == "" ? null : e.State,
                LocationCode:e.LocationCode,
                ParentLocationId:e.ParentLocation == "" ? null : e.ParentLocation,
                TagId:e.Tag.length <= 0 ? null : Array.prototype.map.call(e.Tag, function(item) { return item.value; }).join(","),
                CampusId:"",
                ClientId:1,
            }
            var jsonObj = JSON.stringify(myObj)
                    DataService.SaveUpdateLocation(myObj).then(function (response) {
                        debugger     
                        if(response.data == '1')
                        {
                           vm.ShowAlert(vm.$t('Location has been added successfully', [vm.$t('Location')]), "success", true, vm.$t("Alert"));
                        }
                        else if(response.data == '2'){
                            vm.ShowAlert(vm.$t('Location has been updated successfully', [vm.$t('Location')]), "success", true, vm.$t("Alert"));
                        }
                        else if(response.data == '0'){
                            vm.ShowAlert(vm.$t('unknown error', [vm.$t('Location')]), "success", true, vm.$t("Alert"));
                        }
                        
                        vm.$parent.CloseLocationPopup();
                    })
                }
            })
        }

    }
}
</script>